.wrapper {
    border: 1px solid #444;
    border-radius: 10px;
    padding: 20px;
    margin-top: 25px;
    display: block;
    justify-content: center;
    background-color: #e7e7e7;
}

.formWrapper {
    margin-top: 20px;
    max-width: 500px;
}

.formItem {
    width: 100%;
    display: flex;
    flex-direction: column;
    border: 1px solid #444;
    border-radius: 10px;
    padding: 5px;
    background-color: #ffffff;
}

.formItem:nth-child(n + 2) {
    margin-top: 10px;
}

.formItem:hover,
.formItem:focus {
    outline: 2px solid #f1be44;
}

.field {
    border: none;
    background-color: transparent;
}

.field:hover,
.field:focus {
    outline: none;
}

.inputArray:nth-child(n+2) {
    margin-top: 10px;
}

.minBtn {
    background:#B88E2F;
    padding: 2px 5px;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    min-width: 10%;
}

.minBtn:nth-child(n + 3) {
    margin-left: 15px;
}

.btn {
    background:#B88E2F;
    padding: 10px 20px;
    color: #fff;
    border: none;
    border-radius: 5px;
    margin-top: 30px;
    cursor: pointer;
    min-width: 50%;
}